import React, { useState } from "react";
import axios from "axios";
import swal from "sweetalert";
import FormBase from "../components/FormBase/FormBase";
import FormFooter from "../components/FormFooter/FormFooter";
import FormHeader from "../components/FormHeader/FormHeader";
import FormInput from "../components/FormInput/FormInput";
import FormSubmit from "../components/FormSubmit/FormSubmit";
import FormLoader from "../components/FormLoader/FormLoader";
import { API } from "../../config";
import classNames from "./Signup.module.css";
import FormUpload from "../components/FormUpload/FormUpload";

const Signup = ({ history }) => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [image, setImage] = useState("default.jpeg");
  const [loading, setLoading] = useState(false);

  const createUser = () => {
    setLoading(true);
    const data = new FormData();
    data.append("name", name);
    data.append("email", email);
    data.append("password", password);
    data.append("confirmPassword", confirmPassword);
    data.append("image", image);

    axios
      .post(`${API}/signup`, data)
      .then((response) => {
        setLoading(false);
        localStorage.setItem("authToken", response.data.token);
        localStorage.setItem("user", JSON.stringify(response.data.data.user));
        swal(
          "Sign up success!",
          "You will now redirect to the App page!",
          "success",
          {
            button: "Okay",
          }
        ).then(() => {
          history.push("/app");
        });
      })
      .catch((err) => {
        setLoading(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  return (
    <div className={classNames.container}>
      <FormBase>
        <FormHeader title="Sign Up" />
        <div className={classNames.inputs_container}>
          <FormInput
            i={{
              id: "name",
              type: "text",
              placeholder: "Enter your name",
              label: "Your name",
              setFn: setName,
            }}
          />
          <FormInput
            i={{
              id: "email",
              type: "email",
              placeholder: "Enter your email",
              label: "Your e-mail",
              setFn: setEmail,
            }}
          />
          <FormInput
            i={{
              id: "password",
              type: "password",
              placeholder: "********",
              label: "Password",
              setFn: setPassword,
            }}
          />
          <FormInput
            i={{
              id: "confirmPassword",
              type: "password",
              placeholder: "********",
              label: "Confirm Password",
              setFn: setConfirmPassword,
            }}
          />
          <FormUpload
            i={{
              id: "profile_image",
              setFn: setImage,
            }}
          />
          {loading ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Sign up" clickFn={createUser} />
          )}
        </div>
        <FormFooter
          d={{
            title: "Already have an account?",
            btn: "Login",
            btn_link: "/login",
          }}
        />
      </FormBase>
    </div>
  );
};

export default Signup;
